<template>
  <div class="hello">
    <header>
      <h1 v-b-toggle.sidebar-1>Mendofy</h1>
    </header>
    <main>
      
  <div>
    
    <b-sidebar id="sidebar-1" title="Mendofy Info" shadow>
      <div class="px-3 py-2">
        <p>
          Buenas¡¡¡ Gracias por visitar mendofy, esto es un proyecto creado 
          para la asignatura multimedia de la Universidad de Jaén.
        </p>
      </div>
    </b-sidebar>
  </div>
      <img class="circular--square" alt="Vue logo" src="../assets/logo.jpg">
      <section class="player">
        <h2 class="song-title">
          {{actual.titulo}} -
          <span>{{actual.artista}}</span>
        </h2>
        <div class="control">
          <b-button pill variant="outline-secondary" class="mx-2" @click="ant">
            <b-icon icon="skip-start-fill"></b-icon>Anterior
          </b-button>
          <meta http-equiv="Cache-Control" content="no-cache, no-store, must-revalidate" />
<meta http-equiv="Pragma" content="no-cache" />
<meta http-equiv="Expires" content="0" />
          <audio :src="actual.src" ref="foo" controls="true" autoplay> </audio>

          <b-button pill variant="outline-secondary" class="mx-2" @click="sig">
            Siguiente
            <b-icon icon="skip-end-fill"></b-icon>
          </b-button>
        </div>
        <div class="add mt-3">
          
          <av-waveform
            ref-link="foo"  :canv-height="40" :playtime="false"/>
        </div>
      </section>
       <section class="add mt-3">
        <div class="add_button">
          <b-button pill variant="outline-secondary" class="mx-2" v-if="!isplaylist && !isfav" @click="add_playlist">
            Añadir a Playlist
            <b-icon icon="plus-circle-fill"></b-icon>
          </b-button>
          <b-button pill variant="outline-secondary" class="mx-2" v-if="!isplaylist && !isfav" @click="add_fav">
            Añadir a Fav
            <b-icon icon="heart-fill"></b-icon>
          </b-button>
          <b-button pill variant="outline-secondary" class="mx-2" v-if="isplaylist" @click="eliminar_playlist, makeToast('Canción Eliminada de Playlist')">
            Eliminar de Playlist
          </b-button>
          <b-button pill variant="outline-secondary" class="mx-2" v-if="isfav" @click="eliminar_fav, makeToast('Canción Eliminada de Favoritos')">
            Eliminar de Favoritos
            <b-icon icon="skip-end-fill"></b-icon>
          </b-button>
        </div>
      </section>
      <br>
      <section>
        <section class="playlist" >
          <div >
            <b-tabs content-class="mt-3" align="center" >
              <b-tab title="Canciones Disponibles" active> 
                <div>
                  <b-button-group vertical>
                    <!-- recorremos la lista de todas las canciones -->
                    <b-button
                      v-for="song in songs"
                      :key="song.src"
                      @click="play(song)"
                      :class="(song.src == actual.src) ? 'Cacion sonando' : 'song'"
                    >{{song.titulo}} - {{song.artista}}</b-button>
                  </b-button-group>
                </div>
              </b-tab>
              <b-tab title="Mi Playlist">
                <div >
                  <b-button-group vertical>
                    <!-- recorremos la lista de canciones en la playlist -->
                      <b-button
                        v-for="song in Listplay"
                        :key="song.src"
                        @click="play(song)"
                        :class="(song.src == actual.src) ? 'Cacion sonando' : 'song'"
                      >
                        {{song.titulo}} - {{song.artista}}
                      </b-button>
                  </b-button-group>
                </div>
              </b-tab>
              <b-tab title="Fav">
                <div>
                  <b-button-group vertical>
                     <!-- recorremos la lista de canciones Favoritas -->
                      <b-button
                        v-for="song in Listfav"
                        :key="song.src"
                        @click="play(song)"
                        :class="(song.src == actual.src) ? 'Cacion sonando' : 'song'"
                      >
                        {{song.titulo}} - {{song.artista}}
                      </b-button>
                  </b-button-group>

                </div>
              </b-tab>
            </b-tabs>
          </div>
        </section>
      </section>
    </main>
  </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import AvWaveform from '../wave/AvWaveform.js'
export default {
  name: 'Mendofy',
  components: {
    AvWaveform,
  },
  data() {
    return {
      actual: {},
      index: 0,
      isplaying: false,
      isplaylist: false,
      isfav: false,
      mediaNotSupported: false,
      showMedia: false,
      songs: [
        {
          id: 1,
          titulo: "Frozen Desert",
          artista: "iseo & dodosound",
          src: require("../assets/iseo-dodosound-frozen-desert.mp3")
        }
      ],
      songs_playlist: [],

      songs_fav: [],

      player: new Audio()
    };
  },
  methods: {
    listarCanciones() {
      this.axios
        .get("/cancion")
        .then(response => {
          this.songs = response.data;
        })
        .catch(e => {
          console.log("error" + e);
        });
    },
    play(song) {
      if (typeof song.src != "undefined") {
        this.actual = song;
        this.player.src = this.actual.src;
      }

      this.player.play();
      this.isplaying = true;
    },
    pause() {
      this.player.pause();
      this.isplaying = false;
    },
    ant() {
      this.index--;
      if (this.index < 0) {
        this.index = this.songs.length - 1;
      }
      this.actual = this.songs[this.index];
      //this.play(this.actual);
    },
    sig() {
      this.index++;
      if (this.index > this.songs.length - 1) {
        this.index = 0;
      }
      this.actual = this.songs[this.index];
      //this.play(this.actual);
    },
    add_playlist() {
      if(this.songs_playlist.includes(this.actual.id)){
        this.makeToast(' La Canción ya está añadida a Mi Playlist','danger')
      }else{
        this.makeToast('Canción añadida a Mi Playlist','success')
        this.songs_playlist.push(this.actual.id); //añadimos el id de la cancion a la songs_playlist
      }
      
    },
    add_fav() {
      if(this.songs_fav.includes(this.actual.id)){
        this.makeToast(' La Canción ya está añadida a Favoritos','danger')
      }else{
        this.makeToast('Canción añadida a Favoritos','success')
        this.songs_fav.push(this.actual.id); //añadimos el id de la cancion a la songs_fav
      }
    },
    eliminar_playlist() {
      this.songs;
    },
    eliminar_fav() {
      this.songs;
    },
    makeToast( msg = "", variant = "") {
        this.$bvToast.toast(msg, {
          title: `Mendofy Alerta`,
          autoHideDelay: 4000,
          variant: variant,
          appendToast: true
        })
    },
  },
  created() {
    this.listarCanciones();
    this.actual = this.songs[this.index];
    this.player.src = this.actual.src;
  },
computed: {
  

  //Listplay: Funcion que me devuelve la lista de canciones en la playlist
  Listplay: function () {
    var cancion = {};
    var listaPlaylist = [];
    //por cada id que tenemos en la lista songs_playlist filtramos en la lista songs(donde se encuentran todas las canciones)
    //para que nos devuelva la cancion a la que corresponde el id 
    this.songs_playlist.forEach(element => {

        cancion = this.songs.filter(i => i.id === element)

        listaPlaylist.push(cancion[0])

    });
    
    return  listaPlaylist;
    
  },
  //Listfav: Funcion que me devuelve la lista de canciones favoritas
  Listfav: function () {
    var cancion = {};
    var listafav = [];
    //por cada id que tenemos en la lista songs_fav filtramos en la lista songs(donde se encuentran todas las canciones)
    //para que nos devuelva la cancion a la que corresponde el id 
    this.songs_fav.forEach(element => {

        cancion = this.songs.filter(i => i.id === element)

        listafav.push(cancion[0])

    });
    
    return  listafav;
  }
},
mounted () {
    if (!navigator.mediaDevices) {
      this.mediaNotSupported = true
    }
  }
}
</script>

